/* GraphComponent.module.css */
.Graph-container {
    display: flex;
    justify-content: center;
    align-items: center;
    /* これを追加 */
    width: 100%;
    height: 300px;
    /* 画面の高さ全体を使用 */
}

.Graph-left-side {
    width: 50%;
    /* 60%の幅に設定 */
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.Graph-right-side {
    width: 50%;
    /* 40%の幅に設定 */
    padding: 10px;
    box-sizing: border-box;
    position: relative;
}

.NIRGraph {
    max-width: 100%;
    max-height: 300px;
    height: auto;
}

.title {
    font-size: 15px;
    text-align: center;
    margin-bottom: 0px;
    font-weight: 800;
}

.error-message {
    color: red;
    font-weight: bold;
    font-size: 15px;
    margin: 10px 0;
    text-align: center;
}